
import dateFormat from 'vux/src/tools/date/format.js'

import '../../assets/js/clipboard'
import api from '../../assets/js/api'
import {getParams,weChatPay} from '../../assets/js/util'
export default {
components: {},
filters: {
dateFormat
},
data () {
return {
wuliu: '1231231231',
orderId: getParams()['oreder_id'] || '',
detail: {
"id": 1,
"ordersn": "1232324343",
"total": "99999",
"amount": 88,
"freight": "123123",
"rebate": "11",
"delivery": 2,
"province": "广东省",
"city": "广州市",
"district": "天河区",
"address": "瑞东花园",
"username": "马彪",
"phone": 1231232,
"addTime": 1,
"payType": 1,
"payTime": 13412343243,
"deliveryTime": 13412343243,
"finishTime": 13412343243,
"good": [
{
"good_name": "测试商品",
"good_price": "88",
"good_photo": null,
"rebate": "2",
"amount": 1
}
],
"condition": "待审核凭证",
"logistics": [
{


}
],
"makerAddress": [
{
"phone": 12332432,
"province": "广东省",
"city": "广州市",
"district": "黄浦区",
"address": "瑞东花园"
}
]
}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
copy(){
var clipboard = new Clipboard('button');
clipboard.on('success', e => {

this.layer('复制成功');
});
clipboard.on('error', e => {

this.layer('复制失败，请手动复制');
});
},

changeOrderStatus(status){
this.showLoading('提交中')
this.$http.post(api.changeOrderStatus,{
ordersn: this.detail.ordersn,
status: status
}).then(res => {
this.hideLoading()
this.layer('修改成功')
}).catch(e => {
this.hideLoading()
})
},
gotoViewCer(){
this.$router.push({
path: `/viewcer?id=`
})
},
/*跳转微信支付*/
gotoWeChatPay(){
location.href = weChatPay(this.detail.ordersn)
},
/*跳转上传凭证页面*/
uploadVoucher(){
this.$router.push({
path: `/offinePay?order_id=${this.detail.ordersn}&isPost=${this.detail.delivery}`
})
},
/*订单操作删除，通过审核*/
passVerify(type){
this.showLoading('提交中')
this.$http.post(api.verify,{
ordersn: this.detail.ordersn,
type: type
}).then(res => {
this.hideLoading()
this.layer('审核通过')
/* todo:审核通过后的操作 */
}).catch(e => {
this.hideLoading()
})
},
},
mounted() {
this.copy();

document.getElementsByTagName('title')[0].textContent = '订单详情';
}
}



